.info__pay {
	//   float: right;
	width: 100%;
	.header {
		font-weight: bold;
		font-size: 1.5rem;
		background-color: #f1f1f1;
		padding: 10px 20px;
		margin: 0;
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.info__content {
		padding: 0 20px;
		background-color: #f1f1f1;
		.promotion {
			div {
				font-size: 1rem;
			}
			.promotion__content {
				display: flex;
				justify-content: space-between;
				input {
					width: 60%;
					padding-left: 2%;
					height: auto;
				}
				button {
					width: 40%;
					background-color: #f15e2c;
					font-size: 1rem;
					height: auto;
					color: #fff;
					border: none;
				}
			}
		}
		.orderDetail {
			border-top: dashed;
			border-bottom: dashed;
			font-size: 1.2rem;
			div {
				display: flex;
				justify-content: space-between;
				font-weight: bold;
				opacity: 0.7;
			}
			.reduction:last-child {
				font-weight: normal;
			}
		}
		.tempCacul {
			div {
				display: flex;
				justify-content: space-between;
				align-items: center;

				height: 4vw;
				font-weight: bold;
				justify-items: center;
				:first-child {
					font-size: 1.2rem;
					// font-size: 1.8vw;
					opacity: 0.8;
				}
				:last-child {
					font-size: 1.5rem;
				}
			}
			button {
				font-size: 1.4rem;
				width: 100%;
				height: 70px;
				color: #fff;
				background-color: #f15e2c;
				margin: 20px 0;
			}
		}
	}
	.info__content > div {
		padding: 20px 0;
	}
}
button {
	font-weight: bold;
}

